#tab-bar.demo {
    display: flex;
    flex-direction: column;
}

#tab-bar .demoName {
    height: 40px;
}

#tab-bar .demo-preview-item .am-tab-bar {
    background-color: white;
}

.am-tabs-tab-bar-wrap {
    position: fixed;
    bottom: 0px;
    width: 100%;
    align-items: flex-end;
}

.am-tab-bar-bar {
    background-color: white;
    display: flex;
    justify-content: space-around;
    width: 100%;
}

.am-tab-bar-tabP, .am-tab-bar-tab-icon {
    text-align: center;
}

.am-tab-bar-tab-title {
    font-size: 8px;
}

.am-search-cancel{
    display: none;
}

.contain {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 70px;
    background: rgba(0,0,0,0.01);
}

.header {
    display: flex;
    width: 100%;
    justify-content: space-around;
    align-items: center;
    margin-top: 20px;
}

.header > img {
    width: 30px;
    height: 30px;
}

.am-search {
    display: flex;
}

.am-search-cancel am-search-cancel-show {
    display: none;
}

.am-search-synthetic-ph {
    display: none;
}

.am-search-value {
    border: 1px solid #9b9b9b;
    border-radius: 24px;
    width: 240px;
    padding: 16px 8px;
}

.carousel {
    width: 100%;
    margin-top: 16px;
}

.menu {
    width: 100%;
}

.top {
    display: flex;
    justify-content: space-around;
    margin-top: 24px;
}

.bottom {
    display: flex;
    justify-content: space-around;
    margin-top: 16px
}

.module {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.adv {
    display: flex;
    width: 100%;
    justify-content: space-around;
    margin-top: 32px;
}

.adv > .right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.progress {
    width: 100%;
    height: 100px;
    border: 1px solid red;
}

.jp {
    width: 100%;
    margin-top: 40px;
    margin-bottom: 25px;
}
.box {
    width: 100px;
    height: 100px;
    box-shadow: 10px 10px 10px red;
}
.title {
    padding-left: 22px;
    padding-right: 33px;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
}
.jp-top-right {
    font-size: 12px;
}
.example {
    width: 100%;
    display: flex;
    flex-direction: row;
    overflow: auto;
    height: 230px;
}
.example::-webkit-scrollbar { width: 0 !important }

.example > .example-img {
    width: 343px;
    height: 190px;
    border-radius: 5px;
    margin-left: 22px;
    box-shadow: 10px 10px 10px rgba(0,0,0,0.3);
}
.example-img img {
    width: 150px;
}

.hot {
    width: 100%;
}
.hot-right span {
    font-size: 12px;
}
.zheng {
    margin-right: 12px;
}
.check {
    font-weight: bold;
}

.hot-content {
    height: 214px;
    margin: 20px;
    padding: 5px;
    background: white;
    border-radius: 5px;
    box-shadow: 5px 5px 5px rgba(0,0,0,0.3);
}
.tp {
    display: flex;
    justify-content: space-between;
}

.tp-left {
    width: 240px;
    height: 130px;
}
.tp-left img {
    width: 100%;
    height: 100%;
}
.tp-right {
    height: 130px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.tp-img {
    width: 100px;
    height: 50px;
}
.tp-img img {
    width: 100%;
    height: 100%;
}

.hot-title {
    display: block;
    margin-top: 5px ;
    font-size: 16px;
    font-family: "PingFangCS-medium";
}
.hot-desc {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.font-14 {
    display: block;
    margin: 3px 0;
    font-size: 12px;
}
.sign span {
    font-size: 5px;
    padding: 1px 5px;
    border-radius: 25px;
    margin-left: 3px;
}
.price {
    color: red;
}
.guiGe {
    font-size: 8px;
}

